import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Stack } from 'react-bootstrap';
import FlightMethods from '../../utility/flights/FlightMethods';
import Form from 'react-bootstrap/Form';
import { useEffect, useState } from 'react';
import { useFields } from '../../contexts/FieldsContext';
import FilterableCitySelect from './FilterableCitySelect';
import FilterableGateSelect from './FilterableGateSelect';
import FilterableStatusSelect from './FilterableStatusSelect';
import Spinner from 'react-bootstrap/Spinner';
import { toast } from 'react-toastify';
import { useAuth } from '../../contexts/AuthContext';

//This version uses form submit and grabs the variables.  It's a bit simpler, since it doesn't use effects or state

function FlightModal({ flight, show, handleClose }) {
    const { authenticatedFetch } = useAuth();
    const baseApiUrl = import.meta.env.VITE_API_BASE_URL;
    const dailiesBaseUrl = `${baseApiUrl}/v2/api/Dailies`;
    const { byField, fields: fieldsContext, loading: fieldsLoading } = useFields();
    // Only call getFormattedFlight after fieldsContext is loaded
    const formattedFlight = (!fieldsLoading && fieldsContext && Object.keys(fieldsContext).length > 0)
        ? FlightMethods.getFormattedFlight(flight, fieldsContext)
        : {};
    const [city, setCity] = useState("");
    const [status, setStatus] = useState("");
    let cityOptions = byField?.city || [];
    let gateOptions = byField?.gate || [];
    // If the current city code is not in the options, add it as a custom option
    if (city && !cityOptions.some(opt => opt.code === city)) {
        cityOptions = [
            { code: city, value: city },
            ...cityOptions
        ];
    }
    // If the current gate is not in the options, add it as a custom option
    const [gate, setGate] = useState("");
    if (gate && !gateOptions.some(opt => opt.code === gate)) {
        gateOptions = [
            { code: gate, value: gate },
            ...gateOptions
        ];
    }
    const statusOptions = byField?.status || [];

    // Set city value when modal opens or when fields are loaded
    useEffect(() => {
        if (show && formattedFlight.iata) {
            setCity(formattedFlight.iata);
        }
        if (show && formattedFlight.statusCode) {
            setStatus(formattedFlight.statusCode);
        }
        if (show && formattedFlight.gate) {
            setGate(formattedFlight.gate);
        }
    }, [show, formattedFlight.iata, formattedFlight.statusCode, formattedFlight.gate]);

    const updateFlight = (id, dailyValues) => {
        console.log("Updating flight " + id);
        const url = dailiesBaseUrl + '/UpdateDailyValues/' + id;
        const numRecords = 10;
        authenticatedFetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(dailyValues)
        })
        .then(response => {
            if (!response.ok) {
              throw new Error(`HTTP error! Status: ${response.status}`);
            }
            return response.json();
          })
          .then(data => {
            console.log('Updating flight succeeded - Data:', data);
            // Handle the data
            handleClose();
            toast.success("Record was saved successfully");
          })
          .catch(error => {
            console.error('Error Updating flight:', error);
            toast.error("Record did not save successfully.  Error reported: " + error);
            // Handle errors
          });
    }

    function getLinecodeNumber() {
        const result = FlightMethods.getCodeshareField(flight, "linecode") + " " + FlightMethods.getCodeshareField(flight, "number");
        return result;
    }

    function setField(name, value, updatedFields) {

        var dailyValue = {field: name, instance: 1, value: value};
        updatedFields.push(dailyValue);

        flight.fields[name] = [{ instance: 1, value: value }];
    }

    const handleSubmit = (event) => {
        event.preventDefault(); // Prevent default browser refresh
        const updatedFields = [];
        setField("gate", gate, updatedFields);
        setField("city", city, updatedFields);
        setField("status", status, updatedFields);
        updateFlight(flight.id, updatedFields);
    };



    if (fieldsLoading || !fieldsContext || Object.keys(fieldsContext).length === 0) {
        return (
            <Modal show={show} onHide={handleClose} centered>
                <Modal.Body className="text-center py-5">
                    <Spinner animation="border" role="status" />
                    <div className="mt-3">Loading fields...</div>
                </Modal.Body>
            </Modal>
        );
    }

    return (
        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title>{getLinecodeNumber()}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form onSubmit={handleSubmit}>
                  <div className="container-fluid">
                    <div className="row">
                      <div className="col-12 col-lg-6">
                        <Form.Group className="mb-3" controlId="id">
                          <Form.Label>ID</Form.Label>
                          <Form.Control type="text" disabled={true} value={flight.id} />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="linecode_number">
                          <Form.Label>Flight</Form.Label>
                          <Form.Control type="text" disabled={true} defaultValue={formattedFlight.linecodeNumber} />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="schedule_time">
                          <Form.Label>Schedule</Form.Label>
                          <Form.Control type="text" disabled={true} value={formattedFlight.schedule || ''} />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="actual_time">
                          <Form.Label>Actual</Form.Label>
                          <Form.Control type="text" disabled={true} value={formattedFlight.actual || ''} />
                        </Form.Group>
                      </div>
                      <div className="col-12 col-lg-6">
                        <Form.Group className="mb-3" controlId="gate">
                          <Form.Label>Gate</Form.Label>
                          <FilterableGateSelect
                            options={gateOptions}
                            value={gate}
                            onChange={setGate}
                            name="gate"
                            inputId="gate-select"
                          />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="city">
                          <Form.Label>City</Form.Label>
                          <FilterableCitySelect
                            options={cityOptions}
                            value={city}
                            onChange={setCity}
                            name="city"
                            inputId="city-select"
                          />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="status">
                          <Form.Label>Status</Form.Label>
                          <FilterableStatusSelect
                            options={statusOptions}
                            value={status}
                            onChange={setStatus}
                            name="status"
                            inputId="status-select"
                          />
                        </Form.Group>
                      </div>
                    </div>
                  </div>
                  <div className="d-flex flex-wrap gap-2 mt-2">
                    <Button type="submit" variant="primary">
                      Save Changes
                    </Button>
                    <Button variant="secondary" onClick={handleClose}>
                      Cancel
                    </Button>
                  </div>
                </Form>
            </Modal.Body>
            <Modal.Footer>
            </Modal.Footer>
        </Modal>
    );
}

export default FlightModal;